@media (max-width:600px) {
input{
    max-width: 350px;
}
}

@media (max-width:570px) {
input{
    max-width: 300px;
}
}
  
@media (max-width:420px) {
input{
    max-width: 300px;
}
}

@media (max-width:370px) {
input{
    max-width: 280px;
}
}

@media (max-width:320px) {
    input{
        max-width: 180px;
}
}

input{
    margin: 20px 0 0;
    height: 70px;
    border-radius: 20px;
    border: 3px solid rgb(0, 0, 0);
    background-color: transparent;
    color: rgb(255, 255, 255);
    font-size: 20px;
    text-align: center;
    width: 390px;
}

div{
    align-items: center;
    min-height: 100%;
    .addButton{
        min-width: 200px;
        max-width: 200px;
        min-height: 40px;
        background-color: rgb(50, 50, 50);
        position: relative;
        top: 0px;
        left: 0px;
        font-size: 18px;
        border: none;
        margin: 40px 0;
    }
}

